import React, {Component} from 'react';
import DataSet from '@antv/data-set';
import { Chart } from '@antv/g2';
import './RadarChart.css'


class RadarChart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            containerId: (this.props.containerId != null) ? this.props.containerId : 'radar-container',
        }

    }
    drawTables = (param) => {
        const { DataView } = DataSet;

        const data = [
            { item: 'Design', a: 70, b: 30 },
            { item: 'Development', a: 60, b: 70 },
            { item: 'Marketing', a: 50, b: 60 },
            { item: 'Users', a: 40, b: 50 },
            { item: 'Test', a: 60, b: 70 },
            { item: 'Language', a: 70, b: 50 },
            { item: 'Technology', a: 50, b: 40 },
            { item: 'Support', a: 30, b: 40 },
            { item: 'Sales', a: 60, b: 40 },
            { item: 'UX', a: 50, b: 60 },
        ];
        const dv = new DataView().source(data);
        dv.transform({
            type: 'fold',
            fields: ['a', 'b'], // 展开字段集
            key: 'user', // key字段
            value: 'score', // value字段
        });
        const chart = new Chart({
            container: this.state.containerId,
            autoFit: true,
            height: 400,
        });
        chart.data(dv.rows);
        chart.scale('score', {
            min: 0,
            max: 80,
        });
        chart.coordinate('polar', {
            radius: 0.8,
        });
        chart.axis('item', {
            line: null,
            tickLine: null,
            grid: {
                line: {
                    style: {
                        lineDash: null,
                    },
                },
            },
        });
        chart.axis('score', {
            line: null,
            tickLine: null,
            grid: {
                line: {
                    type: 'circle',
                    style: {
                        lineDash: null,
                    },
                },
                alternateColor: 'rgba(0, 0, 0, 0.04)',
            },
        });

        chart
            .point()
            .position('item*score')
            .color('user')
            .shape('circle')
            .size(4)
            .style({
                stroke: '#fff',
                lineWidth: 1,
                fillOpacity: 1,
            });
        chart
            .line()
            .position('item*score')
            .color('user')
            .size(2);
        chart.render();

    }

    componentDidMount(){
        this.drawTables()
    }

    render() {
        return (
            <>
                <div className={"radar-chart-bg"}>
                    <p className={"line-title"}>这是关于图表的描述</p>
                    <div id={this.state.containerId} className={"line-chart-container-bg"}>
                    </div>
                </div>
            </>
        );
    }
}

export default RadarChart;
